import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileFtpUpListResponse } from "../../Model/Response/FileFtpUpListResponse";
import { FileFtpUpResponse } from "../../Model/Response/FileFtpUpResponse";
import { FileFtpUpViewModel } from '../../Model/ViewModel/FileFtpUpViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileFtpUpServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileFtpUpServicesProvider extends FileServicesProvider {
  private FileFtpUps: FileFtpUpListResponse;
  private FileFtpUp: FileFtpUpResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileFtpUp";
    console.log("Hello FileFtpUpServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileFtpUp?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileFtpUp/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileFtpUps = new FileFtpUpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileFtpUps;
    });
  }



  public PostPutAsync(FileFtpUpViewModel:FileFtpUpViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileFtpUp";
    else
    sub_url = "FileFtpUp/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileFtpUpViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileFtpUpViewModel,mode).map(
      item => {
        this.FileFtpUp = new FileFtpUpResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileFtpUp;
      }
    );
  }
}



